'use client';

import { zodResolver } from '@hookform/resolvers/zod';
import { addDays, format, isBefore, startOfToday } from 'date-fns';
import { ptBR } from 'date-fns/locale';
import { Calendar as CalendarIcon, Clock, HelpingHand, User, Lock } from 'lucide-react';
import React, { useState, useMemo, useEffect, useCallback } from 'react';
import { useForm } from 'react-hook-form';
import { z } from 'zod';
import { Button } from '@/components/ui/button';
import { Calendar } from '@/components/ui/calendar';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from '@/components/ui/dialog';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { useToast } from '@/hooks/use-toast';
import { cn } from '@/lib/utils';
import { Label } from '@/components/ui/label';

type Slot = {
  time: string;
  isBooked: boolean;
  bookedBy: string | null;
};

type StoredSchedule = {
  scheduleDate: string;
  slots: Slot[];
  isScheduleDefined: boolean;
  startTime: number;
  endTime: number;
};

const bookingFormSchema = z.object({
  name: z.string().min(2, { message: 'O nome deve ter pelo menos 2 caracteres.' }).max(50),
});

const adminAuthSchema = z.object({
  password: z.string().min(1, { message: 'A senha é obrigatória.' }),
});

const LOCAL_STORAGE_KEY = 'prayerScheduleData';

export function PrayerSchedule() {
  const [scheduleDate, setScheduleDate] = useState<Date | undefined>(new Date());
  const [slots, setSlots] = useState<Slot[]>([]);
  const [selectedSlot, setSelectedSlot] = useState<Slot | null>(null);
  const [isBookingDialogOpen, setIsBookingDialogOpen] = useState(false);
  const [isAdminMode, setIsAdminMode] = useState(false);
  const [isAuthDialogOpen, setIsAuthDialogOpen] = useState(false);
  const [isScheduleDefined, setIsScheduleDefined] = useState(false);
  const [startTime, setStartTime] = useState(6);
  const [endTime, setEndTime] = useState(9);

  const { toast } = useToast();

  const bookingForm = useForm<z.infer<typeof bookingFormSchema>>({
    resolver: zodResolver(bookingFormSchema),
    defaultValues: { name: '' },
  });

  const authForm = useForm<z.infer<typeof adminAuthSchema>>({
    resolver: zodResolver(adminAuthSchema),
    defaultValues: { password: '' },
  });

  // Load state from localStorage on initial render
  useEffect(() => {
    try {
      const storedData = localStorage.getItem(LOCAL_STORAGE_KEY);
      if (storedData) {
        const parsedData: StoredSchedule = JSON.parse(storedData);
        const savedDate = new Date(parsedData.scheduleDate);

        // Check if the stored schedule date is in the past
        if (isBefore(savedDate, startOfToday())) {
          localStorage.removeItem(LOCAL_STORAGE_KEY); // Clear expired schedule
        } else {
          setScheduleDate(savedDate);
          setSlots(parsedData.slots);
          setIsScheduleDefined(parsedData.isScheduleDefined);
          setStartTime(parsedData.startTime);
          setEndTime(parsedData.endTime);
        }
      }
    } catch (error) {
      console.error("Failed to load or parse schedule from localStorage", error);
    }
  }, []);

  // Save state to localStorage whenever it changes
  useEffect(() => {
    if (isScheduleDefined) {
      try {
        const dataToStore: StoredSchedule = {
          scheduleDate: scheduleDate!.toISOString(),
          slots,
          isScheduleDefined,
          startTime,
          endTime,
        };
        localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(dataToStore));
      } catch (error) {
        console.error("Failed to save schedule to localStorage", error);
      }
    }
  }, [scheduleDate, slots, isScheduleDefined, startTime, endTime]);


  const bookedSlots = useMemo(() => slots.filter((s) => s.isBooked).sort((a, b) => a.time.localeCompare(b.time)), [slots]);
  const allSlotsBooked = useMemo(() => slots.length > 0 && slots.every((s) => s.isBooked), [slots]);

  const generateTimeSlots = useCallback((start: number, end: number, existingSlots: Slot[] = []): Slot[] => {
    const newSlots: Slot[] = [];
    if (end <= start) return [];

    const existingSlotsMap = new Map(existingSlots.map(s => [s.time, s]));

    for (let i = start; i < end; i++) {
      const startTime = `${String(i).padStart(2, '0')}:00`;
      const endTime = `${String(i + 1).padStart(2, '0')}:00`;
      const time = `${startTime} - ${endTime}`;

      const existingSlot = existingSlotsMap.get(time);

      if (existingSlot) {
        newSlots.push(existingSlot);
      } else {
        newSlots.push({
          time: time,
          isBooked: false,
          bookedBy: null,
        });
      }
    }
    return newSlots;
  }, []);

  const handleSendToWhatsApp = useCallback(() => {
    if (!scheduleDate || bookedSlots.length === 0) return;

    const scheduleText = `*Escala de Oração - ${format(scheduleDate!, 'PPP', { locale: ptBR })}*\n\n${bookedSlots
      .map((s) => `*${s.time}*: ${s.bookedBy}`)
      .join('\n')}\n\nObrigado a todos pela participação!`;

    const phoneNumber = '5577991395904';
    const encodedMessage = encodeURIComponent(scheduleText);
    const whatsappUrl = `https://wa.me/${phoneNumber}?text=${encodedMessage}`;

    window.open(whatsappUrl, '_blank');
  }, [scheduleDate, bookedSlots]);


  useEffect(() => {
    if (allSlotsBooked) {
      handleSendToWhatsApp();
    }
  }, [allSlotsBooked, handleSendToWhatsApp]);


  const handleSelectSlot = (slot: Slot) => {
    if (!slot.isBooked) {
      setSelectedSlot(slot);
      setIsBookingDialogOpen(true);
      bookingForm.reset();
    }
  };

  const handleBookingSubmit = (values: z.infer<typeof bookingFormSchema>) => {
    if (selectedSlot) {
      const updatedSlots = slots.map((s) =>
        s.time === selectedSlot.time ? { ...s, isBooked: true, bookedBy: values.name } : s
      );
      setSlots(updatedSlots);
      setIsBookingDialogOpen(false);
      setSelectedSlot(null);
      toast({
        title: 'Horário Agendado!',
        description: `Obrigado, ${values.name}. Sua hora de oração foi confirmada.`,
        className: 'bg-primary text-primary-foreground',
      });
    }
  };

  const handleAdminAuthSubmit = (values: z.infer<typeof adminAuthSchema>) => {
    if (values.password === '123') {
      setIsAuthDialogOpen(false);
      setIsAdminMode(true);
      authForm.reset();
    } else {
      toast({
        title: 'Senha Incorreta',
        description: 'A senha de administrador está incorreta.',
        variant: 'destructive',
      });
      authForm.reset();
    }
  };

  const handleAdminConfigSubmit = () => {
    if(scheduleDate && startTime < endTime){
      setSlots(generateTimeSlots(startTime, endTime, slots));
      setIsScheduleDefined(true);
      setIsAdminMode(false);
      toast({
        title: "Agenda Definida!",
        description: `A escala para ${format(scheduleDate, 'PPP', { locale: ptBR })} das ${startTime}h às ${endTime}h está disponível.`
      })
    } else {
       toast({
        title: "Erro na Configuração",
        description: "Verifique se a data foi selecionada e se o horário de início é menor que o horário de fim.",
        variant: "destructive"
      })
    }
  };

  const handleAdminButtonClick = () => {
    authForm.reset();
    setIsAuthDialogOpen(true);
  };
  
  if (!isScheduleDefined || isAdminMode) {
    return (
      <div className="space-y-8">
        <Card className="shadow-lg">
          <CardHeader>
            <CardTitle className="flex items-center justify-between">
              <div className="flex items-center gap-2">
                <Lock className="w-6 h-6" />
                Área do Administrador
              </div>
              {isAdminMode && (
                 <Button variant="outline" onClick={() => {
                   setIsAdminMode(false);
                   authForm.reset();
                 }}>
                   {isScheduleDefined ? "Voltar para Escala" : "Sair"}
                 </Button>
              )}
            </CardTitle>
            <CardDescription>
              {isAdminMode
                ? 'Configure a data e o intervalo de horários para a escala de oração.'
                : 'Acesse para configurar a escala de oração.'}
            </CardDescription>
          </CardHeader>
          {isAdminMode ? (
            <>
              <CardContent className="space-y-6">
                 <div className="space-y-2">
                  <Label>1. Escolha o Dia da Oração</Label>
                  <Popover>
                    <PopoverTrigger asChild>
                      <Button
                        variant={'outline'}
                        className={cn(
                          'w-[280px] justify-start text-left font-normal',
                          !scheduleDate && 'text-muted-foreground'
                        )}
                      >
                        <CalendarIcon className="mr-2 h-4 w-4" />
                        {scheduleDate ? format(scheduleDate, 'PPP', { locale: ptBR }) : <span>Escolha uma data</span>}
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0">
                      <Calendar
                        mode="single"
                        selected={scheduleDate}
                        onSelect={(date) => {
                          const oldDate = scheduleDate;
                          setScheduleDate(date);
                          // Reset slots if date changes, otherwise keep them
                          if (date && oldDate && date.getTime() !== oldDate.getTime()) {
                            setSlots(generateTimeSlots(startTime, endTime));
                          }
                        }}
                        initialFocus
                        locale={ptBR}
                        disabled={(day) => day < addDays(new Date(), -1)}
                      />
                    </PopoverContent>
                  </Popover>
                 </div>
                 <div className="space-y-2">
                   <Label>2. Defina o Intervalo de Horários</Label>
                   <div className="flex items-center gap-4">
                      <div className="flex items-center gap-2">
                          <Label htmlFor="start-time">Início</Label>
                          <Input id="start-time" type="number" className="w-20" value={startTime} onChange={(e) => setStartTime(parseInt(e.target.value, 10))} min="0" max="23" />
                          <span>h</span>
                      </div>
                       <div className="flex items-center gap-2">
                          <Label htmlFor="end-time">Fim</Label>
                          <Input id="end-time" type="number" className="w-20" value={endTime} onChange={(e) => setEndTime(parseInt(e.target.value, 10))} min="1" max="24" />
                          <span>h</span>
                      </div>
                   </div>
                 </div>
              </CardContent>
              <CardFooter>
                 <Button onClick={handleAdminConfigSubmit} className="w-full">
                  Salvar e Disponibilizar Agenda
                </Button>
              </CardFooter>
            </>
          ) : (
             <CardFooter>
               <Button onClick={handleAdminButtonClick} className="w-full">
                {isScheduleDefined ? "Editar Escala" : "Acessar como Admin"}
              </Button>
            </CardFooter>
           )}
        </Card>

        {/* Auth Dialog */}
        <Dialog open={isAuthDialogOpen} onOpenChange={setIsAuthDialogOpen}>
          <DialogContent className="sm:max-w-[425px]">
            <DialogHeader>
              <DialogTitle>Acesso Restrito</DialogTitle>
              <DialogDescription>
                Por favor, insira a senha de administrador para continuar.
              </DialogDescription>
            </DialogHeader>
            <Form {...authForm}>
              <form onSubmit={authForm.handleSubmit(handleAdminAuthSubmit)} className="space-y-8 p-4">
                <FormField
                  control={authForm.control}
                  name="password"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Senha</FormLabel>
                      <FormControl>
                        <Input type="password" placeholder="Digite a senha" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <DialogFooter>
                  <Button type="submit">
                    Autenticar
                  </Button>
                </DialogFooter>
              </form>
            </Form>
          </DialogContent>
        </Dialog>
      </div>
    );
  }


  return (
    <div className="space-y-8">
      <Card className="shadow-lg">
        <CardHeader>
           <CardTitle className="flex items-center justify-between">
            <div className='flex items-center gap-2'>
              <Clock className="w-6 h-6" />
              Horários para {scheduleDate ? format(scheduleDate, 'PPP', { locale: ptBR }) : ''}
            </div>
             <Button variant="ghost" size="sm" onClick={handleAdminButtonClick}>
              Admin
            </Button>
          </CardTitle>
          <CardDescription>
            Clique em um horário vago para agendar sua hora de oração.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-4">
            {slots.map((slot) => (
              <Button
                key={slot.time}
                variant={slot.isBooked ? 'secondary' : 'default'}
                className="h-20 flex flex-col items-start p-3 transition-all duration-300 ease-in-out transform hover:scale-105 shadow-md"
                onClick={() => handleSelectSlot(slot)}
                disabled={slot.isBooked}
                style={{
                  backgroundColor: slot.isBooked ? '' : 'hsl(var(--accent))',
                  color: slot.isBooked ? '' : 'hsl(var(--accent-foreground))',
                }}
              >
                <div className="font-bold text-lg">{slot.time}</div>
                <div className="flex items-center gap-1 text-sm mt-1">
                  {slot.isBooked ? (
                    <>
                      <User className="w-4 h-4" />
                      <span>{slot.bookedBy}</span>
                    </>
                  ) : (
                    <>
                      <HelpingHand className="w-4 h-4" />
                      <span>Disponível</span>
                    </>
                  )}
                </div>
              </Button>
            ))}
          </div>
        </CardContent>
      </Card>

      {bookedSlots.length > 0 && (
        <Card className="shadow-lg">
          <CardHeader>
            <CardTitle>Escala de Oração do Dia</CardTitle>
            <CardDescription>
              Abaixo está a lista de irmãos comprometidos com a oração para o dia selecionado.
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead className="w-[150px]">Horário</TableHead>
                  <TableHead>Nome do Membro</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {bookedSlots.map((slot) => (
                  <TableRow key={slot.time}>
                    <TableCell className="font-medium">{slot.time}</TableCell>
                    <TableCell>{slot.bookedBy}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      )}

      <Dialog open={isBookingDialogOpen} onOpenChange={setIsBookingDialogOpen}>
        <DialogContent className="sm:max-w-[425px]">
          <DialogHeader>
            <DialogTitle>Agendar Horário de Oração</DialogTitle>
            <DialogDescription>
              Você está agendando o horário das {selectedSlot?.time}. Por favor, insira seu nome para confirmar.
            </DialogDescription>
          </DialogHeader>
          <Form {...bookingForm}>
            <form onSubmit={bookingForm.handleSubmit(handleBookingSubmit)} className="space-y-8 p-4">
              <FormField
                control={bookingForm.control}
                name="name"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Seu Nome</FormLabel>
                    <FormControl>
                      <Input placeholder="Digite seu nome completo" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <DialogFooter>
                <Button type="submit" className="bg-accent text-accent-foreground hover:bg-accent/90">
                  Confirmar Agendamento
                </Button>
              </DialogFooter>
            </form>
          </Form>
        </DialogContent>
      </Dialog>

      {/* Auth Dialog for Admin Edit */}
      <Dialog open={isAuthDialogOpen} onOpenChange={setIsAuthDialogOpen}>
        <DialogContent className="sm:max-w-[425px]">
          <DialogHeader>
            <DialogTitle>Acesso Restrito</DialogTitle>
            <DialogDescription>
              Por favor, insira a senha de administrador para continuar.
            </DialogDescription>
          </DialogHeader>
          <Form {...authForm}>
            <form onSubmit={authForm.handleSubmit(handleAdminAuthSubmit)} className="space-y-8 p-4">
              <FormField
                control={authForm.control}
                name="password"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Senha</FormLabel>
                    <FormControl>
                      <Input type="password" placeholder="Digite a senha" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <DialogFooter>
                <Button type="submit">Autenticar</Button>
              </DialogFooter>
            </form>
          </Form>
        </DialogContent>
      </Dialog>
    </div>
  );
}
